import Vuex from "vuex";
import axios from "axios";
import http from '@/utils/http.js'

export default {
	data() {
		return {
			goods: []
		}
	},
	created() { //此处用created相当于对前端页面数据进行初始化  
		var calss = uni.getStorageSync('enName')
		var value = uni.getStorageSync('goodsName');
		var address = 'http://120.55.87.80/server/' + uni.getStorageSync('enName') + 'Goods.php';
		http.post(address, value).then(res => {
			//这里是ES6的写法，get请求的地址
			this.goods = res.data; //获取数据  
			console.log('success');
			console.log(this.goods);
			this.$refs.loading.close();
		})
	},
	onReady() {
		this.$refs.loading.open()
	},
	methods: {
		toShop(e) {
			uni.setStorageSync('webUrl', e.address);
			uni.navigateTo({
				url: '../shopweb/shopweb',
				animationType: 'pop-in',
				animationDuration: 200
			});
		}
	}
}
